.App {
  text-align: center;
}

.App-header { display: inline-block; flex-direction: column; align-items: center; justify-content: center; line-height: 2em;  width: 90%; color: #241F20;}
@media screen and (min-width: 1025px) { body { font-size: calc(8vh + 2vmin); margin: 20vh 5vh 5vh 5vh; }}
@media screen and (max-width: 1024px) { body { font-size: calc(5vh + 2vmin); margin-top: 4.5em;}}
@media screen and (max-width: 600px) { body { font-size: calc(3vh + 3vmin); margin-top: 6em; }}



h1 {
  margin-top: 0em;
}


h1#apology.smaller {
  font-size: calc(7vh + 3vmin);
  line-height: 1em; 
}

.img.hidden {
  display: none;
}

.text #desc.hidden {
  display: none;
}

p.hidden {
  display: none;
}
.text #desc {
  display: inline-block;
  align-items: center;
  font-size: calc(1vh + 1vmin);
  margin-top: 1em;
  width: 80%;
  line-height: 1.2em;
  font-weight: bold;
  color: #241F20;
}

.body {
  transition: all 1s ease-in-out;
}
.text {
  transition: all 1s ease-in-out;
  text-align:center;
}

